@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poiret+One:400&display=swap);

p{
    font-size: 18px;
}
@media only screen and (max-width: 768px){
    .center-block{
        margin: 5em 0 5em 0
    }
    
}
@media only screen and (min-width: 768px){
    .center-block{
        margin: 10em 15em 0 15em 
    }
    
}
.icons-links a{
    text-decoration: none;
    padding: 5px;
    color:rgb(255, 221, 31);
    border-radius: 3px;
}

.custom-nav{
    background-color: #7c9962;
    font-size: 18px;
}
.item{
    color:white !important;
    text-decoration: none !important;
}
.item:hover{
    color:rgb(85, 83, 83) !important;
    transition: 0.2s;
}
.custom-footer{
    background-color: black;
    margin-bottom: 0;
}
.footer{
    position: fixed;
  left: 0;
  bottom: 1px;
  width: 100%;
  color: white;
  text-align: center;
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}
body {
    font: 400 14px Quicksand, sans-serif;
    background-color: #35383e;
    color: white;
    /*
    background: url(assets/background-photo.jpg) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    */
}
::selection { 
    background: #ba64f7;
}
.color-nav{
    background-color:#7c9962;
}

.active{
    background-color: #7c9962 !important;
    border-radius: 5px;
}


hr{
    background-color: white;
    margin-left: 5px;
}
h1{
    padding-top: 30px;
    font: 400 40px Poiret One, sans-serif;
}
.nav-brand{
    color: #fff !important;
    font-size: 30px;
}
p{
    display: block;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}
.paragrafo-container p {
    margin: 2em 0 2em 0;
}

